<template>
    <div>
        <div style="display: flex;justify-content: space-between">
            <el-button icon="el-icon-plus" type="primary" @click="showAddSalaryView">添加工资账套</el-button>
            <el-button icon="el-icon-refresh" type="success" @click="initSalaries"></el-button>
        </div>
        <div style="margin-top: 10px;">
            <el-table :data="salaries" border stripe>
                <el-table-column type="selection" width="50"></el-table-column>
                <el-table-column width="120" prop="name" label="账套名称"></el-table-column>
                <el-table-column width="70" prop="basicsalary" label="基本工资"></el-table-column>
                <el-table-column width="70" prop="trafficsalary" label="交通补助"></el-table-column>
                <el-table-column width="70" prop="lunchsalary" label="午餐补助"></el-table-column>
                <el-table-column width="70" prop="bonus" label="奖金"></el-table-column>
                <el-table-column width="150" prop="createdate" label="启用时间"></el-table-column>
                <el-table-column label="养老金" align="center">
                    <el-table-column width="70" prop="pensionper" label="奖金"></el-table-column>
                    <el-table-column width="70" prop="pensionbase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="医疗保险" align="center">
                    <el-table-column width="70" prop="medicalper" label="比率"></el-table-column>
                    <el-table-column width="70" prop="medicalbase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="公积金" align="center">
                    <el-table-column width="70" prop="accumulationfundper" label="比率"></el-table-column>
                    <el-table-column width="70" prop="accumulationfundbase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button @click="showEdit(scope.row)">编辑</el-button>
                        <el-button @click="handleDelete(scope.row)" type="danger">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog
                    :title="title"
                    :visible.sync="dialogVisible"
                    width="50%">
                <div style="display: flex;justify-content: space-around;align-items: center">
                    <el-steps direction="vertical" :active="activeIndex">
                        <el-step :title="itemName" v-for="(itemName,index) in salaryItemName" :key="index"></el-step>
                    </el-steps>
                    <el-input @keydown.enter.native="qulify" v-model="salaryValue[title]" :placeholder="'请输入'+salaryItemName[index]+'...'" v-for="(value,title,index) in salaryValue" :key="index" v-show="index==activeIndex" style="width: 300px"></el-input>
                </div>
                <span slot="footer" class="dialog-footer">
                <el-button @click="cancleall">取消</el-button>
                <el-button @click="preStep">{{activeIndex==10?'取消':'上一步'}}</el-button>
                <el-button type="primary" @click="qulify">{{activeIndex==10?'完成':'下一步'}}</el-button>
              </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalSob",
        data(){
            return{
                title:'添加工资账套',
                dialogVisible:false,
                activeIndex:0,
                salaries:[],
                salaryItemName:[
                    '基本工资',
                    '交通补助',
                    '午餐补助',
                    '奖金',
                    '养老金比率',
                    '养老金基数',
                    '医疗保险比率',
                    '医疗保险基数',
                    '公积金比率',
                    '公积金基数',
                    '账套名称'
                ],
                salaryValue:{
                    basicsalary:0,
                    trafficsalary:0,
                    lunchsalary:0,
                    bonus:0,
                    pensionper:0,
                    pensionbase:0,
                    medicalper:0,
                    medicalbase:0,
                    accumulationfundper:0,
                    accumulationfundbase:0,
                    name:''

                }
            }
        },
        mounted(){
            this.initSalaries();
        },
        methods:{


            handleDelete(data){
                this.$confirm('此操作将永久删除【' + data.name + '】个数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/sob/"+data.id).then(resp=>{
                        if(resp){
                            this.initSalaries();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            cancleall(){
                this.dialogVisible=false;
                this.activeIndex=0;
            },
            preStep(){
                if(this.activeIndex==10){
                    this.dialogVisible=false;
                    this.activeIndex=0;
                    return;
                }
                this.activeIndex--;
            },
            qulify(){
                if(this.salaryValue.id!=null){
                    if(this.activeIndex==10){
                        this.putRequest("/salary/sob/",this.salaryValue).then(resp=>{
                            if(resp){
                                this.initSalaries();
                                this.dialogVisible=false;
                                this.salaryValue={
                                    basicsalary:0,
                                    trafficsalary:0,
                                    lunchsalary:0,
                                    bonus:0,
                                    pensionper:0,
                                    pensionbase:0,
                                    medicalper:0,
                                    medicalbase:0,
                                    accumulationfundper:0,
                                    accumulationfundbase:0
                                };
                                this.activeIndex=0;
                            }
                        })

                    }
                }else{
                    if(this.activeIndex==10){
                        this.postRequest("/salary/sob/",this.salaryValue).then(resp=>{
                            if(resp){
                                this.initSalaries();
                                this.dialogVisible=false;
                                this.salaryValue={
                                    basicsalary:0,
                                    trafficsalary:0,
                                    lunchsalary:0,
                                    bonus:0,
                                    pensionper:0,
                                    pensionbase:0,
                                    medicalper:0,
                                    medicalbase:0,
                                    accumulationfundper:0,
                                    accumulationfundbase:0,
                                    name:''
                                };
                                this.activeIndex=0;
                            }
                        })

                    }
                }
                this.activeIndex++;

            },
            showEdit(data){
                this.title="编辑账套信息";
                this.salaryValue.basicsalary=data.basicsalary;
                this.salaryValue.trafficsalary=data.trafficsalary;
                this.salaryValue.lunchsalary=data.lunchsalary;
                this.salaryValue.bonus=data.bonus;
                this.salaryValue.pensionper=data.pensionper;
                this.salaryValue.pensionbase=data.pensionbase;
                this.salaryValue.medicalper=data.medicalper;
                this.salaryValue.medicalbase=data.medicalbase;
                this.salaryValue.accumulationfundper=data.accumulationfundper;
                this.salaryValue.accumulationfundbase=data.accumulationfundbase;
                this.salaryValue.name=data.name;
                this.salaryValue.id=data.id;
                this.dialogVisible=true;
            },
            showAddSalaryView(){
                this.title="添加账套信息";
              this.dialogVisible=true;
            },
            initSalaries(){
                this.getRequest("/salary/sob/").then(resp=>{
                        if(resp){
                            this.salaries=resp;
                        }
                })
            }

        }
    }
</script>

<style scoped>

</style>