<template>
    <div>
        <div>
            <el-table :data="emps" border stripe>
                <el-table-column type="selection" align="left" width="50"></el-table-column>
                <el-table-column prop="name" label="姓名" fixed width="120" align="left"></el-table-column>
                <el-table-column prop="workid" label="工号" fixed width="120" align="left"></el-table-column>
                <el-table-column prop="email" label="电子邮件" fixed width="200" align="left"></el-table-column>
                <el-table-column prop="phone" label="电话号码" fixed width="120" align="left"></el-table-column>
                <el-table-column prop="department.name" label="所属部门" fixed width="120" align="left"></el-table-column>
                <el-table-column align="center" label="工资账套">
                    <template slot-scope="scope">
                        <el-tooltip placement="right" v-if="scope.row.salary">
                            <div slot="content">
                                <table>
                                    <tr>
                                        <td>基本工资</td>
                                        <td>{{scope.row.salary.basicsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>交通补助</td>
                                        <td>{{scope.row.salary.trafficsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>午餐补助</td>
                                        <td>{{scope.row.salary.lunchsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>奖金</td>
                                        <td>{{scope.row.salary.bonus}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金奖金</td>
                                        <td>{{scope.row.salary.pensionper}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金基数</td>
                                        <td>{{scope.row.salary.pensionbase}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险比率</td>
                                        <td>{{scope.row.salary.medicalper}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险基数</td>
                                        <td>{{scope.row.salary.medicalbase}}</td>
                                    </tr>
                                    <tr>
                                        <td>公积金比率</td>
                                        <td>{{scope.row.salary.accumulationfundper}}</td>
                                    </tr>
                                    <tr>
                                        <td>公积金基数</td>
                                        <td>{{scope.row.salary.accumulationfundbase}}</td>
                                    </tr>
                                </table>
                            </div>
                            <el-tag>{{scope.row.salary.name}}</el-tag>
                        </el-tooltip>
                        <el-tag v-else>暂未设置</el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="操作">
                    <template slot-scope="scope">
                        <el-popover
                                placement="left"
                                width="200"
                                title="修改工资账套"
                                @show="showName(scope.row.salary)"
                                @hide="hideName(scope.row)"
                                trigger="click">
                            <div>
                                <el-select v-model="actionName" placeholder="请选择">
                                    <el-option
                                            v-for="item in salaryName"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </div>
                            <el-button slot="reference" type="danger">修改工资账套</el-button>
                        </el-popover>

                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: flex-end">
            <el-pagination
                    background
                    @size-change="sizeChange"
                    @current-change="currentChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalSobCfg",
        data(){
            return {
                emps:[],
                salaryName:[],
                actionName:null,
                total:0,
                size:10,
                page:1
            }
        },
        mounted(){
            this.initEmps();
            this.initSalaryName();
        },
        methods:{
            sizeChange(sizeChange){
                this.size=sizeChange;
                this.initEmps();
            },
            currentChange(currentChange){
                this.page=currentChange;
                this.initEmps();
            },
            hideName(data){
                if(this.actionName){
                    this.putRequest("/salary/sobcfg/?eid="+data.id+"&sid="+this.actionName).then(resp=>{
                        this.initEmps();
                    })
                }

            },
            showName(data){
                if(data){
                    this.actionName=data.id;
                }else{
                    this.actionName=null;
                }
            },
            initSalaryName(){
                this.getRequest("/salary/sobcfg/salary").then(resp=>{
                    if(resp){
                        this.salaryName=resp;
                    }
                })
            },
            initEmps(){
                this.getRequest("/salary/sobcfg/?page=" + this.page + "&size=" + this.size).then(resp=>{
                    if(resp){
                        this.emps=resp.data;
                        this.total=resp.tatol;
                    }
                })
            }
        }

    }
</script>

<style scoped>

</style>